import './ResetConfirm.scss';
import React, { useState, useEffect } from 'react';
import * as yup from 'yup';
import { useFormik } from 'formik';
import { TextField, Button, Stack, InputAdornment, IconButton, Typography, useMediaQuery } from '@mui/material';
import { appapi } from '../../../api/apiClient';
import { showmessage } from '../../../utils/toastr';
import { useParams, useNavigate } from 'react-router-dom'
import { buttonStyle1 } from '../../../utils/commonStyle';
import { colors } from '../../../utils/colors';
import mobilewavybg from '../../../assets/images/mobilewavybg.svg';
import diztextlogo from '../../../assets/images/diztextlogo.svg';
import wavesbg from '../../../assets/images/wavesbg.svg';
import VisibilityOutlinedIcon from '@mui/icons-material/VisibilityOutlined';
import VisibilityOffOutlinedIcon from '@mui/icons-material/VisibilityOffOutlined';
import SocialCard from '../../../components/SocialCard';
import dizlogoMobile from '../../../assets/images/dizlogoMobile.svg';
import { useDispatch } from 'react-redux';
import { setLoading } from '../../../redux/features/appSlice';

/**
 * Validation Schema for register
 */
const validationSchema = yup.object({
    password: yup
        .string('Enter your Password')
        // .min(8, 'Password must be minimum of 6 characters')
        // .max(12, 'Password must be maximum of 12 characters')
        .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)(?=.*[@$!%*?&])[A-Za-z\d@$!%*?&]{8,12}$/, 'Enter a password of minimum 8 and maximum 12 characters with a combination of capital letters, small letters, symbols, and numbers.')
        .required('Password is requried')
        .trim(),
    reConfirmPassword: yup
        .string()
        .oneOf([yup.ref('password'), null], 'Password must match')
        .required('Retype Password is requried')
        .trim(),
})

export default function ResetConfirm() {

    const paramsFromUrl = useParams()
    const navigate = useNavigate()

    const dispatch = useDispatch()

    const isMobile = useMediaQuery('(max-width:1000px)');

    const [visibility1, setVisibility1] = useState(false);
    const [visibility2, setVisibility2] = useState(false);
    const [data, setData] = useState({
        password: '',
        reConfirmPassword: ''
    })

    const [message, setMessage] = useState({
        status: '',
        msg: ''
    });

    const formik = useFormik({
        enableReinitialize: true,
        initialValues: data,
        validationSchema: validationSchema,
        validateOnBlur: true,
        validateOnChange: true,
        onSubmit: (values) => {
            reset(values)
        },
    })

    /**
     * New password creation
     * @param {password, reconfirmPassword} obj 
     */
    const reset = async (obj) => {
        let params = {
            params: {
                verificationToken: paramsFromUrl.verificationToken
            }
        }
        let payload = {
            password: obj.password,
        }
        const response = await appapi.post(`/auth/reset-password`, payload, params)
        if (response.ok) {
            const data = response.data;
            if (data.status === 'success') {
                showmessage(data.message)
                navigate('/login')
            } else {
                showmessage(data.message, 2000, 'error')
            }
        } else {
            showmessage(`Something Went Wrong!`, 2000, 'error')
        }
    }

    /**
   * 
   * @returns Redirect to initial page which is login
   */
    const onClickLoginRoute = () => navigate(`/login`)

    const handleVerificationToken = async () => {
        dispatch(setLoading(true))
        let params = {
            params: {
                verificationToken: paramsFromUrl.verificationToken
            }
        }
        const response = await appapi.post(`/auth/verifyResetToken`, {}, params);
        if (response.ok) {
            const data = response.data;
            dispatch(setLoading(false))
            setMessage({
                status: data.status,
                msg: data.message
            })
        } else {
            dispatch(setLoading(false))
            // showmessage(`Something Went Wrong!`, 2000, 'error')
        }
    }

    useEffect(() => {
        handleVerificationToken()
    }, []);

    const ResetForm = ({ handleSubmit, values, handleChange, errors, touched, handleBlur, isValid, dirty }) => {
        return (
            <>
                <Typography className='fw-bold ff-Roboto createHeading' color={colors.primaryTxt}> Reset your Password </Typography>
                <form style={{ minHeight: '484px' }} onSubmit={handleSubmit}>
                    <Stack>
                        <Stack className='mt-9 passwordField'>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Password</Typography>
                            </div>
                            <TextField
                                fullWidth
                                id='password'
                                name='password'
                                value={values.password.trim() || ''}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                type={visibility1 ? 'text' : 'password'}
                                // error={touched.password && Boolean(errors.password)}
                                className={values.password ? 'helperTextStyleblack' : 'helperTextStylered'}
                                helperText={(touched.password || dirty.password) && errors.password}
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position='end'>
                                            {
                                                <IconButton onClick={() => setVisibility1(!visibility1)}>
                                                    {visibility1 ? <VisibilityOutlinedIcon /> : <VisibilityOffOutlinedIcon />}
                                                </IconButton>
                                            }
                                        </InputAdornment>
                                    )
                                }}
                            />
                        </Stack>
                        <Stack className='mt-9 passwordField'>
                            <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }}>
                                <Typography className='fs-14 ff-Roboto fw-medium mb-7' color={colors.blackMain}>Retype Password</Typography>
                            </div>
                            <TextField
                                fullWidth
                                id='reConfirmPassword'
                                name='reConfirmPassword'
                                value={values.reConfirmPassword.trim() || ''}
                                onChange={handleChange}
                                onBlur={handleBlur}
                                type={visibility2 ? 'text' : 'password'}
                                // error={touched.reConfirmPassword && Boolean(errors.reConfirmPassword)}
                                className={values.reConfirmPassword ? 'helperTextStyleblack' : 'helperTextStylered'}
                                helperText={(touched.reConfirmPassword || dirty.reConfirmPassword) && errors.reConfirmPassword}
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position='end'>
                                            {
                                                <IconButton onClick={() => setVisibility2(!visibility2)}>
                                                    {visibility2 ? <VisibilityOutlinedIcon /> : <VisibilityOffOutlinedIcon />}
                                                </IconButton>
                                            }
                                        </InputAdornment>
                                    )
                                }}
                            />
                        </Stack>
                        <Button sx={{ ...buttonStyle1, marginBottom: '0' }} disabled={!(dirty && isValid)} type='submit'>Reset</Button>
                    </Stack>
                </form>
            </>
        )
    }

    return (
        <div className='resetConfirmContainer' style={{ padding: isMobile ? '16px' : '0', minHeight: isMobile ? 'auto' : '100vh' }}>
            {
                isMobile ?
                    <div className='mobileViewContainer' style={{ minHeight: 'calc(100vh - 32px)' }}>
                        <div style={{ backgroundImage: `url(${mobilewavybg})`, minHeight: 'calc(100vh - 32px)', backgroundPosition: 'center bottom', backgroundRepeat: 'no-repeat' }}>
                            <div className='logoImg' style={{ marginLeft: '8px' }}>
                                <img alt='dizLogo' src={dizlogoMobile} />
                            </div>
                            {
                                message.status && message.status !== 'success' ?
                                    <div className='innerContainer' style={{ marginTop: '240px' }}>
                                        <Stack>
                                            <Typography className='fw-bold ff-Roboto createHeading' color={colors.primaryTxt}> {message.status ? message.status === 'success' ? 'Successful!' : 'Failed!' : ''} </Typography>
                                            <Typography className='fs-12 ff-Roboto fw-regular' color={colors.blackMain} style={{ marginBottom: '6px' }}>
                                                {message.msg}
                                            </Typography>
                                            <Button sx={{ ...buttonStyle1, textTransform: 'capitalize', marginBottom: '0' }} onClick={onClickLoginRoute} type='submit'>Go Back To Login</Button>
                                        </Stack>
                                    </div>
                                    :
                                    <div className='innerContainer' style={{ marginTop: '136px' }}>
                                        {ResetForm({ ...formik })}
                                    </div>
                            }

                        </div>
                    </div>
                    :
                    <Stack style={{ maxWidth: '1368px', margin: 'auto', minHeight: '758.64px' }} direction='row' justifyContent='space-between'>
                        <Stack className='leftStack' style={{ width: '100%' }}>
                            <div className='logoImg'>
                                <img style={{ visibility: 'hidden' }} alt='dizLogo' src={diztextlogo} />
                            </div>
                            <div className='detailContainer' style={{ backgroundImage: `url(${wavesbg})` }}>
                            </div>
                            <Stack className='formSubmittedStyle'>
                                <div className='logoImg'>
                                    <img alt='dizLogo' src={diztextlogo} />
                                </div>
                                {
                                    message.status && message.status !== 'success' ?
                                        <div style={{ marginTop: '22.916666666667vh' }}>
                                            <Stack>
                                                <Typography className='fw-bold ff-Roboto createHeading' color={colors.primaryTxt}> {message.status ? message.status === 'success' ? 'Successful!' : 'Failed!' : ''} </Typography>
                                                <Typography className='fs-12 ff-Roboto fw-regular' color={colors.blackMain} style={{ marginBottom: '6px' }}>
                                                    {message.msg}
                                                </Typography>
                                                <Button sx={{ ...buttonStyle1, textTransform: 'capitalize', marginBottom: '0' }} onClick={onClickLoginRoute} type='submit'>Go Back To Login</Button>
                                            </Stack>
                                        </div> :
                                        <div style={{ marginTop: '5.2083333333333vh' }}>
                                            {ResetForm({ ...formik })}
                                        </div>
                                }

                            </Stack>
                        </Stack>
                    </Stack>
            }
            {
                isMobile ?
                    <Stack className='footer' direction='row' justifyContent='space-between'>
                        <Stack className='socialContainer' direction='row'>
                            {
                                <SocialCard />
                            }
                        </Stack>
                        <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do it Zero Inc.</Typography>
                    </Stack>
                    :
                    <Stack className='footerWeb'>
                        <Stack className='socialContainer flex-justifycenter' style={{
                            margin: '3.125vh auto 3.125vh'
                        }} direction='row'>
                            {
                                <SocialCard />
                            }
                        </Stack>
                        <Typography className='fs-10 ff-Roboto fw-medium black dizcopyrights'>© 2022 Do it Zero Inc.</Typography>
                    </Stack>
            }
        </div>
    )
}
